import React, {useCallback} from "react";
import {Helper} from "../../constants/Helper";

const TotalCell = React.memo(({element, month, year, setHoverData, isFinance}) => {
    const period = `${year}-${Helper.zeroPad(month + 1, 2)}`;

    const handleHover = useCallback((type) => (e) => {
        const {left, top, height} = e.target.getBoundingClientRect();
        const data = {
            position: {x: left - 280, y: top - 60 + height / 2},
            bg: type === 'debit' ? "bg-success" : type === 'credit' ? "bg-danger" : "bg-secondary",
            user_id: element.user.id,
            date: period,
            ...(type === 'debit' ? {
                count: element.debit_hours,
                sum: element.debit_amount,
                is_payed: 1
            } : type === 'credit' ? {
                count: element.credit_hours,
                sum: element.credit_amount,
                is_payed: 0
            } : {
                count: element.debit_hours + element.credit_hours,
                sum: element.debit_amount + element.credit_amount,
                is_payed: null
            })
        };

        setHoverData(data);
    }, [element, period, setHoverData]);

    return (
        <td className="disable final">
            <div className="summary-cell">
                <div
                    className="is-payed"
                    onMouseOver={handleHover('debit')}
                    onMouseLeave={() => setHoverData(null)}
                >
                    {isFinance ? element.debit_amount : element.debit_hours}
                </div>
                <div
                    className="un-payed"
                    onMouseOver={handleHover('credit')}
                    onMouseLeave={() => setHoverData(null)}
                >
                    {isFinance ? element.credit_amount : element.credit_hours}
                </div>
                <div
                    className="total"
                    onMouseOver={handleHover('total')}
                    onMouseLeave={() => setHoverData(null)}
                >
                    {isFinance
                        ? element.credit_amount + element.debit_amount
                        : element.credit_hours + element.debit_hours}
                </div>
            </div>
        </td>
    );
});

export {TotalCell}