import React, {useCallback, useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {Button, Form, Modal} from "react-bootstrap";
import {useCalendarContext} from "../../hooks/CalendarContext";

const MyModal = React.memo(({show, data, onClose, onConfirm}) => {
    const {t} = useTranslation();
    const [orders, setOrders] = useState([]);
    const [formData, setFormData] = useState({
        order_id: null,
        count: null,
        date: null,
        price: null,
        user_id: null,
        is_payed: 0
    });
    const {getOrders} = useCalendarContext()

    useEffect(() => {
        if (data?.user_id) {
            getOrders(data.user_id).then(result => {
                if (result) setOrders(result)
            });
        }
    }, [data]);

    useEffect(() => {
        setFormData(data);
    }, [data]);

    const handleChange = useCallback((field, value) => {
        setFormData(prev => ({...prev, [field]: value}));
    }, []);

    return (
        <Modal show={show} onHide={onClose} size="lg">
            <Modal.Header>
                <Modal.Title>{t("calendar.hourly_tracking")}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form.Group>
                    <Form.Label column={true}>{t("calendar.order")}</Form.Label>
                    <Form.Control
                        as="select"
                        value={formData?.order_id || ''}
                        onChange={(e) => handleChange('order_id', e.target.value)}
                    >
                        <option value="">{t("calendar.select_order")}</option>
                        {orders.map(item => (
                            <option key={item.id} value={item.id}>
                                #{item.id}
                            </option>
                        ))}
                    </Form.Control>
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Label column={true}>{t("calendar.quantity")}</Form.Label>
                    <Form.Control
                        type="number"
                        value={formData?.count || ''}
                        onChange={(e) => handleChange('count', parseInt(e.target.value))}
                    />
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Label column={true}>{t("calendar.cost")}</Form.Label>
                    <Form.Control
                        type="text"
                        value={formData?.price || ''}
                        disabled
                    />
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Check
                        type="checkbox"
                        label={t("calendar.paid")}
                        checked={formData?.is_payed === 1}
                        onChange={(e) => handleChange('is_payed', e.target.checked ? 1 : 0)}
                    />
                </Form.Group>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={onClose}>
                    {t("calendar.cancel")}
                </Button>
                <Button
                    variant="success"
                    onClick={() => onConfirm(formData)}
                    disabled={!formData?.count}
                >
                    {t("calendar.save")}
                </Button>
            </Modal.Footer>
        </Modal>
    );
});

export {MyModal}