import React from "react";
import {useTranslation} from "react-i18next";

const Popup = React.memo(({data, isFinance}) => {
    const {t} = useTranslation();

    if (!data) return null;

    return (
        <div
            className={`popup ${data.bg} show`}
            style={{
                top: data.position.y,
                left: data.position.x,
            }}
        >
            <div className="row w-100">
                <div className="col-5 text-center text-white"><b>{t("calendar.popup_date")}</b></div>
                <div className="col-7 text-white">{data.date}</div>
            </div>
            {data.order_id && (
                <div className="row w-100">
                    <div className="col-5 text-center text-white"><b>{t("calendar.popup_order_id")}</b></div>
                    <div className="col-7 text-white">#{data.order_id}</div>
                </div>
            )}
            {data.price && (
                <div className="row w-100">
                    <div className="col-5 text-center text-white"><b>{t("calendar.popup_price")}</b></div>
                    <div className="col-7 text-white">{data.price}</div>
                </div>
            )}
            {data.is_payed !== null && (
                <div className="row w-100">
                    <div className="col-5 text-center text-white"><b>{t("calendar.popup_status")}</b></div>
                    <div className="col-7 text-white">
                        {data.is_payed ? t("calendar.is_paid") : t("calendar.is_not_paid")}
                    </div>
                </div>
            )}
            <div className="row w-100">
                <div className="col-5 text-center text-white"><b>{t("calendar.popup_count")}</b></div>
                <div className="col-7 text-white">{data.count}</div>
            </div>
            <div className="row w-100">
                <div className="col-5 text-center text-white"><b>{t("calendar.amount")}</b></div>
                <div className="col-7 text-white">{data.sum}</div>
            </div>
        </div>
    );
});

export {Popup}