export const Helper = {
    // url: "http://build.local",
    url: "https://build.amgcompany.ru",
    types: {
        more: 'Больше',
        less: 'Меньше',
        equal: 'Равно',
        'not-equal': 'Не равно'
    },
    priorities: [
        'Низкий приоритет',
        'Средний приоритет',
        'Высокий приоритет'
    ],
    status: [
        'Выключен',
        'Активен',
        'Отключен'
    ],
    findById: (array, value) => {
        return array.find(item => {
            return item.id === value
        })
    },
    buildForm: (data) => {
        console.log(data)
        const form = new FormData();

        for (const key in data) {
            form.append(key, data[key]);
        }
        return form
    },
    zeroPad: (num, places) => String(num).padStart(places, '0'),
    FETCH_CONFIG: {
        headers: new Headers({Authorization: `Bearer ${window.token}`}),
        credentials: "include"
    }
}