import React, {useCallback} from "react";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {Helper} from "../../constants/Helper";
import {isBefore} from "date-fns";

const CalendarCell = React.memo(({index, element, currentDate, onClick, setHoverData, isFinance}) => {
    const {month, year, findByDate, getPrice} = useCalendarContext();
    const dateStr = `${year}-${Helper.zeroPad(month + 1, 2)}-${Helper.zeroPad(index + 1, 2)}`;
    const day = findByDate(element.hours, dateStr);
    const newDate = new Date(year, month, index + 1);

    const sum = day ? (parseInt(day.count) || 0) * (parseInt(day.price) || 0) : 0;
    const isPast = isBefore(newDate, currentDate);

    const handleClick = useCallback(() => {
        if (day) {
            onClick(day);
        } else if (isPast) {
            getPrice(element.user.id, dateStr).then((result) => {
                onClick({"price":result.price,"user_id":result.user_id,"date":dateStr, "is_payed":false})
            });
        }
    }, [day, element, dateStr, isPast, onClick]);

    const handleMouseOver = useCallback((e) => {
        if (!day || !isPast) return;

        const {left, top, width, height} = e.target.getBoundingClientRect();
        const popupWidth = 280;
        const popupHeight = 180;

        // Расчет позиции по X
        const x = left + width + popupWidth > window.innerWidth
            ? left - (popupWidth / 2 - width / 2)
            : left - (popupWidth / 2 - width / 2);

        // Расчет позиции по Y с проверкой на выход за нижнюю границу
        const yPosition = top + height + 5 + popupHeight;
        const y = yPosition > window.innerHeight
            ? top - popupHeight - 5  // Показываем над ячейкой
            : top + height + 5;      // Показываем под ячейкой

        setHoverData({
            bg: day.is_payed ? "bg-success" : "bg-danger",
            position: {x, y},
            ...day,
            sum
        });
    }, [day, isPast, sum, setHoverData]);

    if (!isPast) {
        return <td className="disable" />;
    }

    if (day) {
        return (
            <td
                key={`${element.user.id}-${dateStr}`}
                className={day.is_payed ? "is-payed" : "un-payed"}
                onClick={handleClick}
                onMouseOver={handleMouseOver}
                onMouseLeave={() => setHoverData(null)}
            >
                {isFinance ? sum : day.count}
            </td>
        );
    }

    return (
        <td
            key={`${element.id}-${dateStr}`}
            className="free"
            onClick={handleClick}
        >
            0
        </td>
    );
});

export {CalendarCell}